.popup {
	display: none;
	&--active {
		display: block;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(#000, 0.5);
		z-index: 100; }
	&__wrap {
		position: absolute;
		top: calc(50% - 125px);
		left: calc(50% - 150px);
		width: 300px; }
	& form {
		display: flex;
		flex-direction: column;
		background: #ededed;
		padding: 0 20px;
		text-align: center;
		font-size: 22px; }
	&__input {
		padding: 10px;
		border: none;
		border-radius: 15px; }
	&__title {}
	&__submit {
		margin-top: 20px;
		margin-bottom: 50px;
		align-self: center;
		border: none;
		background: #BC273B;
		color: #fff;
		padding: 10px 15px;
		font-size: 18px;
		cursor: pointer;
		box-shadow: 0 0 20px -7px #000;
		transition: 0.3s; }
	&__submit:hover {
		box-shadow: 0 0 20px -3px #000; } }

@media (min-width: 900px) {
	.popup {
		&__wrap {
			position: absolute;
			top: calc(50% - 175px);
			left: calc(50% - 275px);
			width: 550px; } } }
