.partners {
	margin-top: 72px;
	&__title {
		color: #fff;
		text-align: center;
		font-size: 22px;
		text-transform: uppercase; }
	&__sliders {
		margin: auto;
		margin-top: 40px;
		width: 150px;
		position: relative; } }
#slaid {
	outline: none; }
#slaid img {
	box-sizing: border-box;
	background-color: rgba(#fff, 0.9);
	height: 80px;
	padding: 10px;
	object-fit: contain; }
#slaid:hover img,
#slaid:focus img {
	background-color: rgba(#fff, 0.5); }

.owl-stage {
	display: flex;
	align-items: center; }
.owl-prev {
	position: absolute;
	top: calc(50% - 55px);
	left: -75px;
	width: 70px;
	height: 90px;
	outline: none; }
.owl-next {
	position: absolute;
	top: calc(50% - 55px);
	right: -60px;
	width: 70px;
	height: 90px;
	outline: none; }
.owl-prev:before,
.owl-next:before {
	content: '';
	position: absolute;
	top: calc(50% - 20px);
	left: calc(50% - 10px);
	width: 50px;
	height: 50px; }
.owl-prev:before {
	background: url(../img/arrow-left.png) no-repeat; }
.owl-next:before {
	background: url(../img/arrow-right.png) no-repeat; }
.owl-prev:hover {
	opacity: 0.3; }
.owl-next:hover {
	opacity: 0.3; }

@media (min-width: 600px) {
	.partners {
		&__sliders {
			width: 469px; } } }

@media (min-width: 800px) {
	.partners {
		&__sliders {
			width: 669px; } } }
