.nav {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 10px 0;
	&__logo {
		height: 58px; }
	&__wrap-contact {
		display: flex;
		flex-direction: column;
		text-align: center;
		margin: 20px 0; }
	&__number {
		color: #000;
		font-size: 20px;
		line-height: 25px;
		outline: none; }
	&__number:hover {
		color: #BC273B; }
	&__number:focus {
		color: #BC273B; }
	&__email {
		color: #BC273B;
		font-size: 12px;
		outline: none; }
	&__email:hover {
		color: #000; }
	&__email:focus {
		color: #000; }
	&__btn {
		box-sizing: border-box;
		width: 174px;
		padding: 10px 0;
		background: transparent;
		border: 2px solid #BC273B;
		color: #BC273B;
		font-size: 10px;
		outline: none; }
	&__btn:hover {
		color: #fff;
		background: #BC273B; }
	&__btn:focus {
		color: #fff;
		background: #BC273B; } }

@media (min-width: 600px) {
	.nav {
		flex-direction: row;
		&__wrap-contact {
			margin: 0;
			margin-left: auto;
			align-self: flex-end;
			text-align: left; }
		&__btn {
			margin-left: 54px; } } }
