.form {
	position: relative;
	&__form {
		display: flex;
		flex-direction: column;
		width: 300px;
		margin-right: auto;
		color: #fff; }
	&__wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	&__wrap input {
		background: transparent;
		border: none;
		border-bottom: 2px solid #fff; }
	&__wrap input::placeholder {
		color: #fff;
		text-transform: uppercase;
		font-size: 14px; }
	&__wrap input:hover {
		border-color: #BC273B; }
	&__wrap input:focus {
		border-color: #BC273B; }
	&__name {
		color: #fff;
		outline: none; }
	&__email {
		margin-top: 10px;
		color: #fff;
		outline: none;
		z-index: 10; }
	&__btn {
		margin-top: 35px;
		color: #fff;
		background: #BC273B;
		border: none;
		padding: 10px;
		font-size: 12px;
		cursor: pointer;
		text-transform: uppercase;
		outline: none;
		z-index: 10; }
	&__btn:hover {
		color: #BC273B;
		background: #fff; }
	&__btn:focus {
		color: #BC273B;
		background: #fff; } }

@media (min-width: 800px) {
	.form {
		&__form {
			margin-top: 30px;
			width: 469px; }
		&__wrap {
			flex-direction: row; }
		&__email {
			margin-top: 0;
			width: 48%; }
		&__name {
			width: 48%; } } }

@media (min-width: 1200px) {
	.form {
		&__form {
			margin: auto;
			margin-top: 30px;
			width: 669px; } } }
