.main {
	&__wrapper {
		height: 800px;
		padding-top: 30px;
		background-image: url(../img/flooded-no.jpg);
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover; }
	&__title {
		color: #fff;
		text-align: center; }
	&__title h1 {
		font-size: 38px; }
	&__title h2 {
		margin-top: 32px;
		font-size: 22px; }
	&__controls {
		position: absolute;
		top: 160px;
		left: -20px; }
	&__controls::before {
		content: '';
		position: absolute;
		top: 30%;
		left: 100px;
		width: 30px;
		height: 30px;
		background: url(../img/icon.png) no-repeat; }
	&__controls::after {
		content: '';
		position: absolute;
		top: 21%;
		right: -20px;
		width: 30px;
		height: 30px;
		transform: rotate(180deg);
		background: url(../img/icon.png) no-repeat; } }

.main__controls:hover .main__toggle {
	opacity: 0.5;
	cursor: pointer; }

#product-1:checked ~ .main__wrapper {
	background-image: url("../img/flooded.jpg"); }

@media (min-width: 600px) {
	.main {
		&__wrapper {
			height: 700px;
			padding-top: 130px; }
		&__title h1 {
			font-size: 53px; }
		&__title h2 {
			margin-top: 72px; }
		&__controls {
			top: 0;
			left: 320px; } } }

@media (min-width: 800px) {
	.main {
		&__controls {
			top: 0;
			left: 440px; } } }


@media (min-width: 1200px) {
	.main {
		&__controls {
			top: 40px;
			left: 850px; } } }
