body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'MullerBold', sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.container {
  width: 300px;
  margin: 0 auto; }

@media (min-width: 600px) {
  .container {
    width: 580px;
    margin: 0 auto; } }

@media (min-width: 800px) {
  .container {
    width: 700px;
    margin: 0 auto; } }

@media (min-width: 1200px) {
  .container {
    width: 1100px;
    margin: 0 auto; } }

@font-face {
  font-family: 'font8b23c9a4968caba7c3e5a1601c2444e3';
  src: url("https://nomail.com.ua/files/eot/8b23c9a4968caba7c3e5a1601c2444e3.eot?#iefix") format("embedded-opentype"), url("https://nomail.com.ua/files/woff/8b23c9a4968caba7c3e5a1601c2444e3.woff") format("woff"), url("https://nomail.com.ua/files/woff2/8b23c9a4968caba7c3e5a1601c2444e3.woff2") format("woff2"); }

