.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0; }
  .nav__logo {
    height: 58px; }
  .nav__wrap-contact {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 20px 0; }
  .nav__number {
    color: #000;
    font-size: 20px;
    line-height: 25px;
    outline: none; }
  .nav__number:hover {
    color: #BC273B; }
  .nav__number:focus {
    color: #BC273B; }
  .nav__email {
    color: #BC273B;
    font-size: 12px;
    outline: none; }
  .nav__email:hover {
    color: #000; }
  .nav__email:focus {
    color: #000; }
  .nav__btn {
    box-sizing: border-box;
    width: 174px;
    padding: 10px 0;
    background: transparent;
    border: 2px solid #BC273B;
    color: #BC273B;
    font-size: 10px;
    outline: none; }
  .nav__btn:hover {
    color: #fff;
    background: #BC273B; }
  .nav__btn:focus {
