@media (min-width: 600px) {
  .nav {
    flex-direction: row; }
    .nav__wrap-contact {
      margin: 0;
      margin-left: auto;
      align-self: flex-end;
      text-align: left; }
    .nav__btn {
      margin-left: 54px; } }

.main__wrapper {
  height: 800px;
  padding-top: 30px;
  background-image: url(../img/flooded-no.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.main__title {
  color: #fff;
  text-align: center; }

.main__title h1 {
  font-size: 38px; }

.main__title h2 {
  margin-top: 32px;
  font-size: 22px; }

.main__controls {
  position: absolute;
  top: 160px;
  left: -20px; }

.main__controls::before {
  content: '';
  position: absolute;
  top: 30%;
  left: 100px;
  width: 30px;
  height: 30px;
  background: url(../img/icon.png) no-repeat; }

.main__controls::after {
  content: '';
  position: absolute;
  top: 21%;
  right: -20px;
  width: 30px;
  height: 30px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  background: url(../img/icon.png) no-repeat; }

.main__controls:hover .main__toggle {
  opacity: 0.5;
  cursor: pointer; }

#product-1:checked ~ .main__wrapper {
  background-image: url("../img/flooded.jpg"); }

@media (min-width: 600px) {
  .main__wrapper {
    height: 700px;
    padding-top: 130px; }
  .main__title h1 {
    font-size: 53px; }
  .main__title h2 {
    margin-top: 72px; }
  .main__controls {
    top: 0;
    left: 320px; } }

@media (min-width: 800px) {
  .main__controls {
    top: 0;
    left: 440px; } }

@media (min-width: 1200px) {
  .main__controls {
    top: 40px;
    left: 850px; } }

.partners {
  margin-top: 72px; }
  .partners__title {
    color: #fff;
    text-align: center;
    font-size: 22px;
    text-transform: uppercase; }
  .partners__sliders {
    margin: auto;
    margin-top: 40px;
    width: 150px;
    position: relative; }

#slaid {
  outline: none; }

#slaid img {
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.9);
  height: 80px;
  padding: 10px;
  -o-object-fit: contain;
     object-fit: contain; }

#slaid:hover img,
#slaid:focus img {
  background-color: rgba(255, 255, 255, 0.5); }

.owl-stage {
  display: flex;
  align-items: center; }

.owl-prev {
  position: absolute;
  top: calc(50% - 55px);
  left: -75px;
  width: 70px;
  height: 90px;
  outline: none; }

.owl-next {
  position: absolute;
  top: calc(50% - 55px);
  right: -60px;
  width: 70px;
  height: 90px;
  outline: none; }

.owl-prev:before,
.owl-next:before {
  content: '';
  position: absolute;
  top: calc(50% - 20px);
  left: calc(50% - 10px);
  width: 50px;
  height: 50px; }

.owl-prev:before {
  background: url(../img/arrow-left.png) no-repeat; }

.owl-next:before {
  background: url(../img/arrow-right.png) no-repeat; }

.owl-prev:hover {
  opacity: 0.3; }

.owl-next:hover {
  opacity: 0.3; }

@media (min-width: 600px) {
  .partners__sliders {
    width: 469px; } }

@media (min-width: 800px) {
  .partners__sliders {
    width: 669px; } }

.form {
