    width: 300px;
    margin-right: auto;
    color: #fff; }
  .form__wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .form__wrap input {
    background: transparent;
    border: none;
    border-bottom: 2px solid #fff; }
  .form__wrap input::-webkit-input-placeholder {
    color: #fff;
    text-transform: uppercase;
