  .form__wrap input:-ms-input-placeholder {
    color: #fff;
    text-transform: uppercase;
    font-size: 14px; }
  .form__wrap input::-ms-input-placeholder {
    color: #fff;
    text-transform: uppercase;
    font-size: 14px; }
  .form__wrap input::placeholder {
    color: #fff;
    text-transform: uppercase;
    font-size: 14px; }
  .form__wrap input:hover {
    border-color: #BC273B; }
  .form__wrap input:focus {
