    width: 48%; } }

@media (min-width: 1200px) {
  .form__form {
    margin: auto;
    margin-top: 30px;
    width: 669px; } }

.popup {
  display: none; }
  .popup--active {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 100; }
  .popup__wrap {
    position: absolute;
    top: calc(50% - 125px);
    left: calc(50% - 150px);
    width: 300px; }
  .popup form {
    display: flex;
    flex-direction: column;
    background: #ededed;
    padding: 0 20px;
    text-align: center;
    font-size: 22px; }
  .popup__input {
    padding: 10px;
    border: none;
    border-radius: 15px; }
  .popup__submit {
    margin-top: 20px;
    margin-bottom: 50px;
    align-self: center;
    border: none;
    background: #BC273B;
    color: #fff;
    padding: 10px 15px;
    font-size: 18px;
    cursor: pointer;
    box-shadow: 0 0 20px -7px #000;
    transition: 0.3s; }
  .popup__submit:hover {
    box-shadow: 0 0 20px -3px #000; }

@media (min-width: 900px) {
  .popup__wrap {
    position: absolute;
    top: calc(50% - 175px);
    left: calc(50% - 275px);
    width: 550px; } }
